import Vue from 'vue'
import VueRouter from 'vue-router'
import { getItem } from '@/utils/storage.js'
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    redirect: '/admin/home'
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import('@/views/Login.vue')
  },
  {
    path: '/admin',
    name: 'Admin',
    component: () => import('@/components/layout/index'),
    children: [
      {
        path: 'home',
        name: 'Home',
        component: () => import('@/views/Home.vue')
      },
      {
        path: 'blog',
        name: 'Blog',
        component: () => import('@/views/blog/Blog.vue')
      },
      {
        path: 'blogContent',
        name: 'BlogContent',
        component: () => import('@/components/blog/BlogContent.vue')
      },
      {
        path: 'blogContent/:id',
        name: 'BlogContent',
        component: () => import('@/components/blog/BlogContent.vue')
      },
      {
        path: 'note',
        name: 'Note',
        component: () => import('@/views/note/Note.vue')
      },
      {
        path: 'resources',
        name: 'Resourses',
        component: () => import('@/views/resourse/Resourse.vue')
      },
      {
        path: 'noteContent/:id',
        name: 'noteContent',
        component: () => import('@/components/note/noteContent.vue')
      },
      {
        path: 'noteContent',
        name: 'noteContent',
        component: () => import('@/components/note/noteContent.vue')
      }
    ]
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function (location) {
  return originalPush.call(this, location).catch(err => err)
}
router.beforeEach((to, form, next) => {
  // to 要访问的路由
  // from 上一个路由
  // next 想要继续访问的路由
  const reg = /^\/admin\/*/
  const userInfo = getItem('userInfo')
  if (reg.test(to.path)) {
    // 判断用户是否登录
    if (userInfo) {
      next()
    } else {
      alert('您未登录,请先登录')
      next('/login')
    }
  } else {
    next()
  }
  // 如果访问不是管理页面 就可以继续访问
  next()
})
export default router
